<template>
	<div class="body">
		<div class="search_group flex_start" >
			<div class="flex_start">
				<div class="search_box search_box1">
					 <!-- @change="searchListData()" -->
					<el-input placeholder="直投项目名称" v-model="project_name" class="" :clearable='true' >
						<i slot="prefix" class="el-input__icon el-icon-search"></i>
					</el-input>
				</div>
				
			</div>
			<div class="flex_start">
				<!-- <div class="search_box">
					<el-input placeholder="认购基金名称" v-model="subscription_name" class="" :clearable='true'>
						<i slot="prefix" class="el-input__icon el-icon-search"></i>
					</el-input>
				</div> -->
				<div class="search_box">
					<el-input placeholder="项目企业名称" v-model="project_com_name" class="" :clearable='true' >
						<i slot="prefix" class="el-input__icon el-icon-search"></i>
					</el-input>
				</div>
				<div class="btn_box">
					<button @click="searchCompany" >搜索</button>
				</div>
			</div>
		</div>
		<div class="main under_fund">
			<div class="under_fund_header flex_between">
				<div class="under_fund_header_tname">
					直投项目总数(&nbsp;{{total}}&nbsp;)
				</div>
				<div class="flex_end">
					<div  @click="downloadExcelData" class="table-group-title-btn btn-blue-border">
						<span class="iconfont icon-daoru"></span>导出excel表格
					</div>
					<div  @click="downloadExcelModule" class="table-group-title-btn btn-blue-border">
						<span class="iconfont icon-daoru"></span>下载项目信息模版
					</div>
					<div class="">
						<label for="upload-fund-input">
							<div class="table-group-title-btn btn-blue-border">
								<span class="iconfont icon-daochu"></span>导入项目信息
							</div>
						</label>
						<input type="file" ref="inputer" id="upload-fund-input" style="display:none" v-on:change="uploadManageCompanyInfoExcel($event)" />
					</div>
					<div class="table-group-title-btn btn-blue" @click="addDirectInvestProject" >
						<span class="iconfont icon-jia"></span>添加新的项目
					</div>

				</div>
			</div>
			<div class="under_fund_main table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%">
					<el-table-column v-for="(tableHeaderitem,index) of tableHeader " sortable  :key="index" :prop="tableHeaderitem.param" :label="tableHeaderitem.text" :width='tableHeaderitem.width'></el-table-column>
					<el-table-column fixed="right" label="操作" width="240" >
						<template slot-scope="scope">
							<el-button @click="setUserPower(scope.row)" type="text" size="small">设置权限</el-button>
							<el-button @click="toFundRunPage(scope.row)" type="text" size="small">项目运行</el-button>
							<el-button @click="view(scope.row)" type="text" size="small">查看</el-button>
							<el-button @click="deleteDirectInfo(scope.row)" type="text" size="small">删除</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
				<!-- <el-pagination prev-text="上一页" next-text="下一页" background layout="prev, pager, next" :total="1000">
				</el-pagination> -->
				<el-pagination v-if="total>0" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :total="total" :current-page="page" :page-size="pageSize"  layout="sizes, prev, pager, next, jumper" >
    			</el-pagination>
			</div>
		</div>
		<!--  设置权限弹窗开始-->
		<el-dialog title="设置可见人员" :visible.sync="isSetPowerDialogShow" width="813px" center :append-to-body='true'>
			<set-power v-bind:have-power-user-ids="havePowerUserIds" v-on:get-set-power-id="getSetPowerIds"></set-power>
			<div slot="footer" class="dialog_footer clear">
				<button class="dialog_btn dialog_btn_cancel" @click="isSetPowerDialogShow=false">取消</button>
				<button class="dialog_btn dialog_btn_submit" @click="setPowerSubmit">确定</button>
			</div>
		</el-dialog>
		<!--设置权限弹窗结束-->
	</div>
</template>

<script>
	import setPower from '../../components/setPower.vue'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils' 
	
	import {getHavePowerUsersApi,setPowerSubmitApi} from '../../config/api'
	import {directGetFundList,directDelFundInfo,directAddExcel,directDownExcel,directExportExcels} from '../../config/apiSj'
	export default {
		data() {
			return {
				//设置权限弹窗是否显示
				isSetPowerDialogShow: false,
				havePowerUserIds:[],
				setPoweFundId:null,
				ids: '', //设置权限时选择的用户ids

				
				//搜索条件参数
				project_name:'',//直投项目名称
				project_com_name:'',//项目企业名称
				// subscription_name:'',//认购基金名称
				
				
				tableHeader:[
					{
						param:'index',
						text:'序号',
						width:'80',
						type:'text'
					},{
						param:'project_name',
						text:'项目名称',
						width:'250',
					},
					// {
					// 	param:'subscription_names',
					// 	text:'认购基金名称',
					// 	width:'350',
					// },
					{
						param:'project_manager_id',
						text:'项目经理',
						width:'120',
					},{
						param:'unit',
						text:'币种',
						width:'100',
					},{
						param:'subscribe',
						text:'认缴(万元)',
						width:'120',
					},{
						param:'paied',
						text:'实缴(万元)',
						width:'120',
					},{
						param:'project_com_name',
						text:'项目企业名称',
						width:'250',
					},{
						param:'industry',
						text:'行业',
						width:'150',
					},{
						param:'fair_value',
						text:'公允价值(万元)',
						width:'180',
					},{
						param:'is_monitor',
						text:'是否舆情监测',
						width:'150',
					},
					{
						param:'project_status',
						text:'项目流程状态',
						width:'150',
					},
					// {
					// 	param:'quit_info',
					// 	text:'退出情况',
					// 	width:'150',
					// },
					{
						param:'real_repay',
						text:'分配本金(万元)',
						width:'200',
					},{
						param:'directProfit',
						text:'分配收益-税前(万元)',
						width:'200',
					},{
						param:'quote_names',
						text:'上市板块',
						width:'250',
					},
				],//表头数据
				tableData: [],//表格数据

				//分页参数
				total:null,
				page:1,
				pageSize:10,

				// 下拉数据
				record_state:[],//所有基金状态
				all_short_name:[], // 所有简称
				all_name:[],// 所有名称
				
			}
		},
		created() {
			this.getListData();
		},
		mounted() {},
		components: {
			setPower
		},
		methods: {
			//删除功能
			deleteDirectInfo(row){
				if (row.auth.auth_delete) {
					let message = "确定要删除该直投项目吗？";
					dialogConfirmHasbtn(message).then(() => {
						directDelFundInfo({
							id:row.id,
						}).then(res => {
							if (res.data.msgNo == 1) {
								dialogConfirm("success", '删除成功');
								let Listlenght = this.tableData.length;
								if(Listlenght>1){
									this.getListData();
								}else{
									if(this.page>1){
										this.page = this.page - 1;
										this.getListData();
									}else{
										this.getListData(this.page);
									}
								}
							}else{
								dialogConfirm("info", res.data.message);
							}
						})
					}).catch(() => {});
				} else{
					dialogConfirm("info", '您无权限操作该条数据');
				}
				
			},
			//查看页面
			view(row){
				if (row.auth.check_operate) {
					this.$router.push({
						name: 'directInvestProjectView',
						query: {
							id: row.id,
						}
					})
				} else{
					dialogConfirm("info", '您无权限操作该条数据');
				}
			},
			//添加
			addDirectInvestProject(){
				this.$router.push({
					name: "directInvestProjectAdd"
				})
			},
			//项目运行
			toFundRunPage(row){
				if (row.auth.fund_operate) {
					this.$router.push({
						name: "directInvestProjectRun",
						query: {
							id: row.id,
							name: encodeURIComponent(row.project_name) ,
							unit:encodeURIComponent(row.unit),
						}
					})
				} else{
					dialogConfirm("info", '您无权限操作该条数据');
				}
				
			},




			//去设置权限
			setUserPower(row) {
				if(row.auth.auth_operate) {
					this.getHavePowerUsers(row.id);
				} else {
					dialogConfirm("info", "您没有设置权限，请联系管理员！");
				}
			},
			//去获取设置的权限数据
			getHavePowerUsers(id) {
				this.setPoweFundId = id;
				getHavePowerUsersApi({
					type: 6,
					data_id: id
				}).then(res => {
					// console.log(res.data);
					if(res.data.msgNo == 1) {
						this.havePowerUserIds = res.data.payload.ids;
						this.isSetPowerDialogShow = true;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取设置权限组件返回来的ids
			getSetPowerIds(idNodes) {
				this.subComponentPostIds = [];
				for(let i = 0, max = idNodes.length; i < max; i++) {
					if(this.subComponentPostIds.indexOf(idNodes[i]) == -1) {
						this.subComponentPostIds.push(idNodes[i])
					}
				}
				var postIds = this.decodeSelectedUserId(this.subComponentPostIds);
				this.ids = postIds.join(',');
			},
			//给加标识的员工id进行解码还原为id数组
			decodeSelectedUserId(data) {
				var user_ids = '';
				var selectedEmployeeId = [];
				var splitArr = [];
				for(var i = 0, max = data.length; i < max; i++) {
					splitArr = data[i].id.split('-');
					if(selectedEmployeeId.indexOf(splitArr[0]) == -1) {
						selectedEmployeeId.push(splitArr[0]);
					}
				}
				return selectedEmployeeId
			},
			//确认按钮调用的函数
			setPowerSubmit() {
				setPowerSubmitApi({
					table_name: 'sg_company',
					type: 6,
					data_id: this.setPoweFundId,
					ids: this.ids
				}).then(res => {
					// console.log(res.data);
					if(res.data.msgNo == 1) {
						this.isSetPowerDialogShow = false;
						dialogConfirm("success", '权限设置成功');
					} else {
						dialogConfirm("info", '权限设置失败');
					}
				})
			},
			




			
			//获取母基金列表信息
			getListData(){
				directGetFundList({
					page: this.page,
					page_size:this.pageSize,
					project_name: this.project_name,
					project_com_name: this.project_com_name,
					// subscription_name: this.subscription_name,
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data  = res.data.payload;
						if(data.data.length>0){
							data.data.forEach((element,index) => {
								element.index = (this.page-1)*this.pageSize+index+1;
								// if(element.is_monitor==1){
								// 	element.is_monitor = '是'
								// }else{
								// 	element.is_monitor = '否'
								// }
							});
						}
						this.total = data.total;
						this.tableData = data.data;
					}
				})
			},
			searchListData(){
				this.page = 1;
				this.subscription_name = '';
				this.project_com_name = '';
				this.getListData()
				
			},
			//搜索按钮点击事件
			searchCompany() {
				this.page = 1;
				// this.project_name = '';
				this.getListData()
			},
			//修改页面条数时调用pageSize
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getListData()
			},
			//修该当前页数时调用currentPage
			changeCurrentPage(e){
				this.page = e;
				this.getListData()
			},





			//导入Excel文件
			uploadManageCompanyInfoExcel() {
				let inputDOM = this.$refs.inputer;
				// 通过DOM取文件数据
				let file = inputDOM.files[0];
				if(file == undefined) {
					return false;
				}
				if(!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
					dialogConfirm('info', '请选择Excel格式文件')
					inputDOM.value = '';
					return false
				}
				directAddExcel({},file).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '导入成功');
						this.page = 1;
						this.getListData();
					} else {
						dialogConfirm('info', '导入失败，请联系开发人员');
					}
					inputDOM.value = '';
				})
			},

			//导出excel表格
			downloadExcelData(){
				directExportExcels({}).then(res => {
					if (res.data.msgNo == 1) {
						dialogConfirm("success", '下载成功');
						window.location.href = res.data.payload.path;
					}else{
						dialogConfirm("info", res.data.message);
					}
				})
			},

			//下载子基金项目信息模版
			downloadExcelModule(){
				directDownExcel().then(res => {
					if (res.data.msgNo == 1) {
						dialogConfirm("success", '下载成功');
						window.location.href = res.data.payload;
					}else{
						dialogConfirm("info", res.data.message);
					}
				})
			},
			
		},
	}
</script>

<style lang="scss" scoped="scoped">
	.body {
		padding: 0 30px 30px 30px;
		font-size: 14px;
	}
	.search_group{
		padding: 20px 0px 30px 0;
		border-bottom: 1px solid #E5E5EA;
	}
	.search_group_bottom{
		margin-top: 20px;
	}
	.search_group_bottom .search_box{
		width: 140px;
	}
	
	.search_box{
		width: 170px;
		height: 30px;
		margin-right: 13px;
	}
	.date_box{
		width: 162px;
	}
	.date_middle_text{
		margin: 0 10px;
	}
	.btn_box{
		margin-left: 24px;
	}
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}	
	.under_fund_header {
		margin: 30px 0;
		color: #6292F4;
		font-size: 14px;
	}
	.under_fund_header>div{
		color: #6292F4;
	}
	.under_fund_header_tname{
		color: #6292F4;
		// padding: 0 4px;
		font-size: 12px;
	}
	// .under_fund_header span {
	// 	color: #6292F4;
	// 	padding: 0 4px;
	// }
	
	.under_fund_header button{
		width: 160px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	.under_fund_header button span{
		color: white;
		font-size: 14px;
	}
	.dialog_btn {
		width: 94px;
		height: 36px;
		color: #6292F4;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.dialog_btn_submit {
		margin-left: 20px;
		color: white !important;
		background: #6292F4;
	}

	.search_row_box .search_btn{
	    margin-left: 76px;
	}
</style>


